<template>
  <div>
    <div>
      <button v-on:click="click1">click</button>
      <button v-on:click.stop="click1">click</button>
      <button v-on:submit.prevent="click1">click</button>
      <button v-on:click.stop.prevent="click1">click</button>
      <button v-on:click.capture="click1">click</button>
      <button v-on:click.self="click1">click</button>
      clickText = {{clickText}}
      <div v-on:scroll.passive="onScroll">...</div>
      <input v-on:keyup.enter="keyup1">
      <input v-on:keyup.page-down="keyup1">
      <input v-on:keyup.13="keyup1">
      keyupText = {{keyupText}}
      <input v-on:blur="blur1">
      blurText = {{blurText}}
      <h1 v-bind:class="{'selected': parabool[i]}">parabool</h1>
      <h1 v-bind:class="{'selected': parabool[i-1]}">parabool</h1>
      <button v-on:click="bool1">true</button>
      <button v-on:click="bool2">false</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DOM',
  data () {
    return {
      clickText: 0,
      keyupText: 0,
      blurText: 0,
      parabool: [],
      i: 0
    }
  },
  methods: {
    click1 () {
      this.clickText += 1
    },
    keyup1 () {
      this.keyupText += 1
    },
    blur1 () {
      this.blurText += 1
    },
    bool1 () {
      this.parabool.push(true)
      this.parabool[this.i] = true
      console.log(this.parabool[1])
      console.log(this.parabool + ' ' + 0)
    },
    bool2 () {
      this.parabool.push(false)
      this.parabool[this.i] = false
      console.log(this.parabool[1])
    }
  }
}
</script>

<style scoped>
  .selected{
    color: red;
    border:2px solid red;
  }
</style>



